.header { 
  margin: 20px 0 0 0;
  position: fixed;
  width: inherit;
  z-index: 3;
}

.header .container .main-nav {
  margin: 0 0 0 auto;
}

.header .container {
  border-radius: 35px;
  background-color: $white;
  box-shadow: 2px 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
  padding: 0 8px;
  justify-content: space-between;
  margin: 0 20px;
}

